// app/layout.js
'use client';

import './globals.css';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useContext } from 'react';
import AuthContext ,{ AuthProvider}  from '@/context/AuthContext';
import Sidebar from '@/components/Dashboard/Sidebar';
import ThemeToggle from '@/components/ThemeToggle';
import { ToastContainer } from 'react-toastify';

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <body>
        <AuthProvider>
          <Layout>{children}</Layout>
        </AuthProvider> 
      </body>
    </html>
  );
}

function Layout({ children }) {
  const { user, logout } = useContext(AuthContext);
  const router = useRouter();

  const handleLogout = () => {
    logout();
    router.push('/login');
  };

  return (
    <div className="layout-container">
     <div className='sidebar'>
        <ThemeToggle/>
    <h1 className="text-xl font-bold">PDL Rentals</h1>
        <p className="text-sm">Welcome, {user?.name || 'Guest'}</p>
        <p className="text-sm">Role: {user?.role || 'Guest'}</p>
       
    <div className="p-4 w-full bg-fuchsia-700 rounded-xl row-span-2 col-span-2">&nbsp;
       <Sidebar user={user} handleLogout={handleLogout} />
      <div className="flex flex-col items-center justify-center h-full">
       
      <div className="main-content flex p-4 w-full  bg-fuchsia-900 rounded-xl row-span-3" style={{"justifyItems":'center'}}>&nbsp;
      <main className="main flex-1 bg-gray-100 p-6 overflow-y-auto">
        <ToastContainer 
        sition="top-right"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        pauseOnHover
        theme="colored" />
                  {children}
                  
                  </main>
      <footer className="w-full bg-gray-900 text-white p-4 text-center">
        <p>&copy; 2023 PDL Rentals. All rights reserved.</p>
        </footer>
        </div>
      </div>
      </div>
    </div>
     </div>

  );
}
   
